<template>
  <div class="sys-main">
    <div class="contentbox">
      <div class="panel">
        <div class="panel-heading">
          <div class="panel-title">质量舆情列表
            <a href="javascript:void(0)" v-show="showPage==1" @click="showPage=2,init(1)">
              <span class="panel-sub-title"><i class="icon-favorite"></i>收藏列表</span>
            </a>
            <a href="javascript:void(0)" v-show="showPage==2" @click="showPage=1,init(1)">
              <span class="panel-sub-title"><i class="icon-reply"></i>返回</span>
            </a>
          </div>
        </div>
        <div class="panel-body">
          <div class="casesearch" v-show="showPage==1">
            <ul>
              <li>
                <span class="spantitle">时间范围：</span>
                <el-select v-model="searchObj.dateType" style="width: 100px;">
                  <el-option v-for="(i, index) in timeList" :key="index" :label="i.label" :value="i.value">
                  </el-option>
                </el-select>
                <el-date-picker v-model="searchObj.custom" type="daterange" range-separator="至" start-placeholder="开始日期"
                  end-placeholder="结束日期" value-format="yyyy-MM-dd" v-show="searchObj.dateType=='custom'" style="float: right; margin-left:5px;">
                </el-date-picker>
              </li>
              <li>
                <span class="spantitle">文章排序：</span>
                <el-select v-model="searchObj.sort" style="width: 120px;">
                  <el-option v-for="(i, index) in orderList" :key="index" :label="i.label" :value="i.value">
                  </el-option>
                </el-select>
              </li>
              <li>
                <span class="spantitle">来源选择：</span>
                <el-checkbox-group v-model="searchObj.source" style="width: 600px; height: 40px; line-height: 40px;">
                  <el-checkbox v-for="(i,index) in sourceList" :key="index" :label="i.name"></el-checkbox>
                </el-checkbox-group>
              </li>
              <li>
                <el-input v-model="searchObj.keyword" placeholder="请输入关键词" @keydown.enter="init(1)"></el-input>
              </li>
              <li>
                <el-button type="primary" icon="el-icon-search" @click="init(1)">查询</el-button>
              </li>
            </ul>
          </div>

          <el-row>
            <el-table :data='item.content' stripe ref='table' style='width: 100%' class="table table-striped" v-loading="loading">
              <el-table-column width="60" show-overflow-tooltip label='序号' fixed>
                <template slot-scope="scope">
                  {{scope.$index + 1 + (nowPage - 1) * pageSize}}
                </template>
              </el-table-column>
              <el-table-column min-width="300" show-overflow-tooltip label='热点内容'>
                <template slot-scope="scope">
                  <h3><a href="javascript:void(0)" @click="dialogVisible=true, activeMsg=scope.row" v-html="scope.row.title"></a></h3>
                  <p v-show="scope.row.keyword!=''">关键字：<span v-html="scope.row.keyword"></span></p>
                </template>
              </el-table-column>
              <el-table-column prop='content' width="500" show-overflow-tooltip label='描述'>
                <template slot-scope="scope">
                  <span v-html="scope.row.content"></span>
                </template>
              </el-table-column>
              <el-table-column prop='source' width="200" show-overflow-tooltip label='来源网站'>
                <template slot-scope="scope">
                  <span v-html="scope.row.source"></span>
                </template>
              </el-table-column>
              <el-table-column prop='publishTime' width="200" show-overflow-tooltip label='发布时间'>
                <template slot-scope="scope">
                  <span v-html="scope.row.publishTime"></span>
                </template>
              </el-table-column>
              <el-table-column width="100" show-overflow-tooltip label='操作' fixed="right">
                <template slot-scope="scope">
                  <div class="oprate">
                    <a href="javascript:void(0)" v-show="!scope.row.favorited" @click="favor(scope.row.id, 1)"><i class="icon-favorite"
                        title="收藏"></i></a>
                    <a href="javascript:void(0)" v-show="scope.row.favorited" @click="favor(scope.row.id, 0)" class="active"><i
                        class="icon-favorite" title="去掉收藏"></i></a>
                  </div>
                </template>
              </el-table-column>
            </el-table>
          </el-row>
          <div class="pagebar" v-show="item.totalRecord>pageSize">
            <div class="pageinfo">共有{{item.totalRecord}}条记录，每页显示{{pageSize}}条</div>
            <el-pagination background layout="prev, pager, next" :total="item.totalRecord" :page-size="pageSize"
              @current-change="init" :current-page.sync="nowPage">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
    <el-dialog :title="activeMsg.title" :visible.sync="dialogVisible" width="30%">
      <span>{{activeMsg.content}}</span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loading: false,
      showPage: 1,
      nowPage: 1,
      pageSize: 10,
      item: {
        content: []
      },
      searchObj: {
        dateType: '',
        from: '',
        to: '',
        source: [],
        sort: 1,
        custom: '',
        keyword: this.$route.query.keyword ? this.$route.query.keyword : ''
      },
      timeList: [{
        label: '全部',
        value: ''
      },
      {
        label: '今日',
        value: 'today'
      },
      {
        label: '24小时',
        value: '24'
      },
      {
        label: '近3天',
        value: '3d'
      },
      {
        label: '近1周',
        value: '1w'
      },
      {
        label: '近1月',
        value: '1m'
      },
      {
        label: '自定义',
        value: 'custom'
      }
      ],
      orderList: [{
        label: '时间降序',
        value: 1
      },
      {
        label: '时间升序',
        value: 2
      },
      {
        label: '采集顺序',
        value: 3
      }
      ],
      sourceList: [],
      activeMsg: {},
      dialogVisible: false
    }
  },
  created () {
    this.getSource()
  },
  methods: {
    init (p) {
      var self = this
      this.item = {
        content: []
      }
      this.nowPage = p
      let params = {}
      let api = 'A_S_SENTIMENT_ITEM'
      if (this.showPage === 2) {
        params = {
          page: p,
          pageSize: this.pageSize
        }
        api = 'A_S_FAVOR_ITEM'
      } else {
        params = this.getParams()
      }
      this.loading = true
      this.$http.get({
        api: api,
        params: params,
        loading: this.loading,
        callback: (res) => {
          self.loading = false
          res.content = self.$lib.highLight(res.content, self.searchObj.keyword)
          self.item = res
        }
      })
    },
    getParams () {
      if (this.searchObj.source.length < 1) {
        this.$bus.$emit('alert', {
          text: '请选择来源！'
        })
        throw new Error('请选择来源！')
      }
      let obj = {
        pageSize: this.pageSize,
        page: this.nowPage,
        dateType: this.searchObj.dateType,
        sort: this.searchObj.sort,
        keyword: this.searchObj.keyword,
        source: this.searchObj.source.join()
      }
      if (obj.dateType === 'custom') {
        let dateArr = this.searchObj.custom
        if (dateArr && dateArr.length > 1) {
          obj.from = dateArr[0]
          obj.to = dateArr[1]
        } else {
          this.$bus.$emit('alert', {
            text: '请选择自定义时间！'
          })
          throw new Error('请选择自定义时间！')
        }
      } else {
        obj.from = ''
        obj.to = ''
      }
      return obj
    },
    del (id) {
      let self = this
      this.$bus.$emit('confirm', {
        text: '此操作将永久删除该信息, 是否继续?',
        callback: () => {
          self.deleteEvent(id)
        }
      })
    },
    deleteEvent (id) {
      let self = this
      this.loading = true
      this.$http.delete({
        api: 'A_S_SENTIMENT_DELETE',
        params: {
          id: id
        },
        loading: this.loading,
        callback: (res) => {
          self.loading = false
          self.init(1)
        }
      })
    },
    favor (id, status) {
      let self = this
      this.$http.get({
        api: 'A_S_SENTIMENT_FAVOR',
        params: {
          id: id,
          eventId: status
        },
        callback: (res) => {
          self.init(1)
        }
      })
    },
    getSource () {
      let self = this
      this.$http.get({
        api: 'A_S_SOURCE_ITEM',
        params: {},
        callback: (res) => {
          res.map(o => {
            self.searchObj.source.push(o.name)
          })
          self.sourceList = res
          self.init(1)
        }
      })
    }
  }
}

</script>

<style scoped>
  .el-pagination {
    margin-top: 20px;
  }

  .el-pagination .number {
    position: initial;
    width: initial;
  }

  .casesearch li span.el-checkbox__inner,
  .casesearch li span.el-checkbox__input {
    width: 14px;
  }

  .panel-title {
    margin-top: 20px;
  }

  .panel-sub-title {
    float: right;
    font-size: 12px;
    font-weight: 300;
    color: #058fcd;
  }

  .el-table--fit {
    border-right: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
  }

</style>



